h2 {
  font-family: 'Lora', 'STSong', 'NSimSun', serif;
  font-size: 1.75em;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', 'STXihei', "microsoft yaHei", 微软雅黑, sans-serif;
  font-size: 1.2em;
  line-height: 1.75em;
}

a,
button {
  text-decoration: none;
  cursor: pointer;
  padding: 0;
  margin: 0;
  font-size: inherit;
  font-family: inherit;
  border: none;
  color: inherit;
  outline: none;
}



