.header-container {
  height: 1200px;
  background-size: 100%;
}

.happy-page {
  background-color: white;
  color: black;
  height: 100%;
}

.happy-page .header-container {
  height: 1700px;
}

.sad-page {
  background-color: black;
  color: white;
  height: 100%;
}

.text-container {
  width: 88%;
  max-width: 660px;
  margin: 180px auto;
}

.buttons {
  position: absolute;
  top: 0;
  left: 0;
  margin: 10px;
}

a.home,
a.other {
  padding: 10px;
}

.daytime-background,
.nighttime-background {
  background: url('daytime.jpg') no-repeat;
  background-size: 100%;
  height: 2000px;
}

.daytime-background {
  background: url('daytime.jpg') no-repeat;
}

.nighttime-background {
  background: url('nighttime.jpg') no-repeat;
}



